import React from "react";
import { ReactElement } from "react";
import { PageHeaderContext } from "../../context/layout";
import {
  AppBar,
  Toolbar,
  IconButton,
  Typography,
  makeStyles,
} from "@material-ui/core";
import { ArrowBackIos } from "@material-ui/icons";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
}));

interface AppHeaderOptions {
  title: string;
  subTitle?: string;
  extra?: Array<ReactElement>;
}
const AppHeader = (props: any) => {
  console.log({ headerProps: props });
  const classes = useStyles();
  return (
    <PageHeaderContext.Consumer>
      {(context: AppHeaderOptions) => {
        return (
          <div className={classes.root}>
            <AppBar position="static">
              <Toolbar>
                <IconButton
                  edge="start"
                  color="inherit"
                  aria-label="menu"
                  onClick={() =>
                    typeof props.history.goBack === "function" && props.history.goBack()
                  }
                >
                  <ArrowBackIos />
                </IconButton>
                <Typography variant="h6" className={classes.title}>
                  {context.title}
                </Typography>
                {context.extra}
              </Toolbar>
            </AppBar>
          </div>
        );
      }}
    </PageHeaderContext.Consumer>
  );
};

export default AppHeader;
