import React, { ReactElement } from "react";
interface DefaultPageHeaderValue {
  title: string;
  subTitle: string;
  extra: Array<ReactElement>;
  onRightIconClick: Function;
  setTitle: Function;
  setOnRightIconClick: Function;
  setExtra: Function;
}

interface DefaultFooterValue {
  footerLinks: Array<{
    path: string;
    label: string;
    icon: ReactElement;
  }>;
  showBottomTabBar: boolean;
  setShowBottomTabBar: Function;
  selectedIndex: number;
  setSelectedIndex: Function;
}

const defaultFooterValue: DefaultFooterValue = {
  footerLinks: [],
  showBottomTabBar: false,
  setShowBottomTabBar: () => null,
  selectedIndex: 0,
  setSelectedIndex: () => null,
};
const defaultPageHeaderValue: DefaultPageHeaderValue = {
  title: "",
  subTitle: "",
  extra: [],
  onRightIconClick: (_: any) => null,
  setTitle: (_: any) => null,
  setOnRightIconClick: (_: any) => null,
  setExtra: (_: any) => null,
};
const PageHeaderContext = React.createContext(defaultPageHeaderValue);
const PageFooterContext = React.createContext(defaultFooterValue);
export { PageHeaderContext, PageFooterContext };
