import React, { ReactElement } from "react";
import { Grid, CircularProgress } from "@material-ui/core";
import AppHeader from "../components/layout/Header";
import { PageHeaderContext, PageFooterContext } from "../context/layout";
import Footer from "../components/layout/Footer";
import { PersonOutline } from "@material-ui/icons";

interface WithPageLayoutInterface {
  title: string;
  subTitle: string;
  onRightIconClick: Function;
  extra: Array<ReactElement>;
  selectedIndex: number;
  showBottomTabBar: boolean;
  showLoading: boolean;
}
const withPageLayout = (Page: any) => {
  return class extends React.Component<{}, WithPageLayoutInterface> {
    constructor(props: any) {
      super(props);
      this.state = {
        title: "Do Report",
        subTitle: "",
        onRightIconClick: () => {},
        extra: [],
        selectedIndex: 0,
        showBottomTabBar: false,
        showLoading: false,
      };
    }

    setShowLoading = (showLoading: boolean): void => {
      this.setState({ showLoading });
    };

    setTitle = (title: string): void => {
      this.setState({ title });
    };
    setExtra = (extra: Array<ReactElement>): void => this.setState({ extra });
    setOnRightIconClick = (onRightIconClick: Function): void =>
      this.setState({
        onRightIconClick,
      });

    setSelectedIndex = (selectedIndex: number): void => {
      this.setState({ selectedIndex });
    };

    setShowBottomTabBar = (showBottomTabBar: boolean): void => {
      this.setState({ showBottomTabBar });
    };

    render() {
      return (
        <>
          <PageHeaderContext.Provider
            value={{
              subTitle: this.state.subTitle,
              title: this.state.title,
              extra: this.state.extra,
              onRightIconClick: this.state.onRightIconClick,
              setOnRightIconClick: this.setOnRightIconClick,
              setTitle: this.setTitle,
              setExtra: this.setExtra,
            }}
          >
            <PageFooterContext.Provider
              value={{
                footerLinks: [
                  {
                    path: "/profile",
                    label: "Profile",
                    icon: <PersonOutline />,
                  },
                ],
                selectedIndex: this.state.selectedIndex,
                showBottomTabBar: this.state.showBottomTabBar,
                setSelectedIndex: this.setSelectedIndex,
                setShowBottomTabBar: this.setShowBottomTabBar,
              }}
            >
              <AppHeader {...this.props} />
              <main>
                {this.state.showLoading && (
                  <Grid
                    style={{ display: "flex" }}
                    alignContent="center"
                    justify="center"
                  >
                    <CircularProgress />
                  </Grid>
                )}
                <Grid>
                  <Page {...this.props} setShowLoading={this.setShowLoading} />
                </Grid>
              </main>
              <Footer />
            </PageFooterContext.Provider>
          </PageHeaderContext.Provider>
        </>
      );
    }
  };
};

export default withPageLayout;
