import React, { ReactElement } from "react";
import { TextField, IconButton, InputAdornment } from "@material-ui/core";
interface AppInputProps {
  placeholder: string;
  value?: string | number;
  label?: string;
  password?: boolean;
  type?: string;
  variant?: any;
  prefix?: ReactElement | null;
  suffix?: ReactElement | null;
  onChangeText?: Function;
}
const AppInput = ({
  value = "",
  label = "",
  password = false,
  placeholder,
  type = "text",
  variant = "outlined",
  prefix = null,
  suffix = null,
  onChangeText = () => null,
}: AppInputProps): ReactElement => {
  return (
    <TextField
      label={label}
      fullWidth
      variant={variant}
      value={value}
      type={type}
      className="do-report-input"
      placeholder={placeholder}
      InputProps={{
        className: "app-input",
        endAdornment: suffix && (
          <InputAdornment position="end">
            <IconButton aria-label="toggle password visibility">
              {suffix}
            </IconButton>
          </InputAdornment>
        ),
        startAdornment: prefix && (
          <InputAdornment position="start">
            <IconButton aria-label="toggle password visibility">
              {prefix}
            </IconButton>
          </InputAdornment>
        ),
      }}
      onChange={(e: any) => onChangeText && onChangeText(e.target.value)}
    />
  );
};
export default AppInput;
