import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import BottomNavigation from "@material-ui/core/BottomNavigation";
import BottomNavigationAction from "@material-ui/core/BottomNavigationAction";
import { PageFooterContext } from "../../context/layout";

const useStyles = makeStyles({
  root: {
    left: 0,
    right: 0,
    bottom: 0,
    position: "absolute",
  },
});

export default () => {
  const classes = useStyles();

  return (
    <PageFooterContext.Consumer>
      {(context) => {
        if (!context.showBottomTabBar) return null;

        return (
          <BottomNavigation
            value={context.selectedIndex}
            onChange={(event, newValue) => {
              context.setSelectedIndex(newValue);
            }}
            showLabels
            className={classes.root}
          >
            {context.footerLinks.map((f) => {
              return <BottomNavigationAction label={f.label} icon={f.icon} />;
            })}
          </BottomNavigation>
        );
      }}
    </PageFooterContext.Consumer>
  );
};
